<template>
  <div class="white">
      <van-button size="large" @click="startH5">startH5</van-button>
      <van-button size="large" @click="startH6">startH6</van-button>
      <van-button size="large" @click="startH7">startH7</van-button>
    <span>111</span>
    <!-- {{idNo}} -->
    <!-- <van-overlay :show="show" >
      <van-loading type="spinner" size="24px" />
    </van-overlay> -->
  </div>
</template>

<script>
  export default {
    data(){
        return {
          show:true,
          idNo:''
        }
    },
    methods:{
      startH5(){
        this.$router.go(0);
      },
      startH6(){
        location.reload();
      },
      startH7(){
        window.location.reload();
      }
    },
    mounted(){
      this.idNo = this.$route.params.idNo;
      alert(this.idNo)
      setTimeout(()=>{
        // eslint-disable-next-line no-undef
        AlipayJSBridge.call('pushWindow', {
          url: 'https://m.taobao.com/', // 要打开页面的 URL
          param:{
            closeCurrentWindow:true
          }
        });
      },2500)
    }
  }
  
</script>

<style>
.van-loading{
  top: 45%;
}
</style>